import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faTwitter,
  faGoogle,
  faGithub,
} from "@fortawesome/free-brands-svg-icons";
import { authService, firebaseInstance } from "fbase";
import AuthForm from "components/AuthForm";

const Auth = () => {
  const onSocialClick = async (event) => {
    const {
      target: { name },
    } = event;
    let provider;
    if (name === "google") {
      provider = new firebaseInstance.auth.GoogleAuthProvider();
    } else if (name === "github") {
      provider = new firebaseInstance.auth.GithubAuthProvider();
    }
    await authService.signInWithPopup(provider);
  };
  const onDownClick = () => {
    window.location.href =
      "https://runningboy02.github.io/nwitter/내나라인민소통망-손전화기에%20내려받기.zip";
  };

  return (
    <div className="authContainer">
      <FontAwesomeIcon
        icon={faTwitter}
        color={"red"}
        size="3x"
        style={{ marginBottom: 30 }}
      />
      <span className="center title">내나라 인민 소통망</span>
      <br />
      <br />
      <AuthForm />
      <span class="center buk">
        ※손전화기 앱에서는 소셜로그인이 작동하지 않습니다.
      </span>
      <br />
      <br />
      <div className="authBtns">
        <button onClick={onSocialClick} name="google" className="authBtn">
          Google로 계속 <FontAwesomeIcon icon={faGoogle} />
        </button>
        <button onClick={onSocialClick} name="github" className="authBtn">
          Github로 계속 <FontAwesomeIcon icon={faGithub} />
        </button>
        <button onClick={onDownClick} className="authBtn">
          <FontAwesomeIcon icon={faTwitter} color={"red"} sixe="2x" />
          Android 설치파일 내려받기
        </button>
      </div>
    </div>
  );
};
export default Auth;
